      </router-link>
    </div>

    <div class="project-page__title">
      <h1 class="mb-5">Extracting Personal Attributes from Product Reviews @ Wonderflow</h1>
    </div>

    <div class="mb-5">
      <b-button
        href="MasterThesis.pdf"
        target="_blank" variant="primary">
        View Report
      </b-button>
    </div>
    
    <div class="project-page__text-block">
      <h4> Project Context </h4>
      <p> 
      Truly understanding customers is key to a successful business. Companies increasingly
      leverage data analytics to uncover insights into their customers' needs, preferences, and
      socio-demographic profiles. This information is essential for enhancing user experiences,
      differentiating from competitors, and creating products that satisfy market demands. Businesses are increasingly
      turning to more innovative approaches. One such approach is the analysis of customer-generated content, such as
      product reviews. Product reviews offer valuable insights into customer preferences and sentiments. Traditionally,
      product reviews have been analyzed primarily on feedback about the product, such as customer opinions,
      satisfaction levels, and sentiments on specific features. However, insights into customers' personal attributes,
      behaviors, and usage contexts have not been thoroughly explored. Addressing this gap presents a valuable
      opportunity to leverage user-generated content for a deeper understanding of customers.
      </p>

      <h4> Personal attributes extraction </h4>
      <p>
      The extraction of personal attributes from product reviews is the focus of this research.
      Personal attributes are specific characteristics that uniquely describe an individual's traits,
      lifestyle, and circumstances, such as physical appearance, profession, household composition, and living
      situation. These attributes could include details such as hair type, skin condition, profession, number of family
      members or pets, and whether they live in an apartment, townhouse or single-family home. By extracting personal
      attributes from product
      reviews, we aim to gain a deeper understanding of the characteristics of customers.
      </p>

      <h4>Approach</h4>
      <p>
      In line with previous research on extracting personal attributes, this study will also use the triplet format to
      store these attributes. However, our approach diverges by addressing some of the key limitations of earlier
      studies, including that previous research often relied on predefined lists of attributes. Inspired by recent
      advancements in pre-trained large language models (LLMs), this work introduces a novel approach for extracting
      personal attributes using these models. Our method aims to extract triplets from product reviews without being
      limited by predefined attribute categories, thus offering a more comprehensive and adaptable solution to capture
      the various personal attributes revealed in customer feedback.
      </p>
      
      <h4> Pipeline </h4>
      <p>
      We developed an end-to-end pipeline that processes raw text and constructs a knowledge graph. The pipeline
      includes text pre-processing, triplet extraction, and post-processing, leveraging LLMs to automate the creation of
      Knowledge Graphs (KGs) from unstructured data. It is designed to be adaptable, scalable, and efficient for a wide
      range of applications.
      </p>
    </div>

